:root {
  --main-light-clr: #adc3db;
  --main-dark-clr: #0d1014;
  --add-dark-clr: #27286e;
  --add-light-clr: #396eb3;
  --accent-clr: #dfdc5a;
  --animation: 400ms cubic-bezier(.215, .61, .355, 1);
}

*, :after, :before {
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--main-light-clr);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.search-form {
  background-color: var(--add-light-clr);
  justify-content: center;
  padding: 20px 0;
  display: flex;
}

.input-form {
  width: 500px;
  color: var(--add-dark-clr);
  border: none;
  outline: none;
  padding: 15px 20px;
  font-size: 20px;
  font-weight: 600;
}

.button-submit {
  cursor: pointer;
  background-color: var(--add-light-clr);
  transition: background-color var(--animation);
  border: none;
  outline: none;
  padding: 10px;
  font-size: 24px;
  font-weight: 600;
}

.button-submit:hover, .button-submit:focus {
  background-color: var(--accent-clr);
}

